import React, { useState } from 'react';
import { Link } from 'react-router-dom';

export default function() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const registerUser = e => {
    e.preventDefault();

    const formData = {
      email,
      password
    }

    let xhr = new XMLHttpRequest();
    const host = 'localhost:5000';

    xhr.open('POST', `http://${host}/register`);
    xhr.setRequestHeader('Content-Type', 'application/json');

    xhr.send(JSON.stringify(formData));
    xhr.onload = function() {
      if (xhr.status != 200) { // анализируем HTTP-статус ответа, если статус не 200, то произошла ошибка
        alert(`Ошибка ${xhr.status}: ${xhr.statusText}`); // Например, 404: Not Found
      } else { // если всё прошло гладко, выводим результат
        console.log(xhr.response);
      }
    };
    xhr.onerror = function() {
      alert("Запрос не удался");
    };
  }

  return (
    <div>
      <h1>Регистрация</h1>

      <div>
        <label>E-mail</label>
        <input onChange={e => setEmail(e.target.value)} placeholder="e-mail" name="email" />
      </div>

      <div>
        <label>Password</label>
        <input onChange={e => setPassword(e.target.value)} placeholder="password" name="password" />
      </div>

      <button onClick={registerUser}>Зарегистрироваться</button>

      <Link to="/login">Уже зарегистрированы? На вход</Link>
    </div>
  );
}